
@font-face {
    font-family: 'Rufina';
    src: url('../../../../fonts/Rufina-Regular.woff2') format('woff2'),
        url('../../../../fonts/Rufina-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Roboto';
    src: url('../../../../fonts/Roboto-Regular.woff2') format('woff2'),
        url('../../../../fonts/Roboto-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Josefin Sans';
    src: url('../../../../fonts/JosefinSans-Thin.woff2') format('woff2'),
        url('../../../../fonts/JosefinSans-Thin.woff') format('woff');
    font-weight: 100;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'Bebas';
    src: url('../../../../fonts/BebasNeue-Regular.woff2') format('woff2'),
        url('../../../../fonts/BebasNeue-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }

@font-face {
    font-family: 'Sansita';
    src: url('../../../../fonts/Sansita-Regular.woff2') format('woff2'),
        url('../../../../fonts/Sansita-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }

  @font-face {
    font-family: 'Proza';
    src: url('../../../../fonts/ProzaLibre-Regular.woff2') format('woff2'),
        url('../../../../fonts/ProzaLibre-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
  }

@mixin flex-row {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  @mixin flex-column {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

.wrapping {
    display: flex;
    flex-direction: column;
    position: relative;
    background: #54666c13;
    height: 240vh;
    .intro-historia {
        height: 70vh;
        @include flex-column();
        background: url(../images/historia.jpg);
        background-size: cover;
        color: rgba(255, 255, 255, 0.767);
        background-position: center;
        justify-content: flex-start;
        box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.699);
        width: 100vw;
        .intro-title {
            margin-top: 40%;
            font-size: 2.5em;
            font-family: 'Bebas';
            letter-spacing: 0.05em;
        }
        .intro-subtitle {
            margin-top: 35%;
            font-size: 1.1em;
            font-family: 'Sansita';
            font-weight: lighter;
        }
       
    }
    .first-slide {
        margin: 0;
        background: white;
        height: 110vh;
        width: 100vw;
        z-index: 4;
        
        position: absolute;
        top: 90vh;
        background: black;
        
        color: white;
        @include flex-column();
        justify-content: flex-start;
        gap: 30vh;
        .first-headers {
            z-index: 4;
            // border: 1px solid white;
            margin-top: 25vh;
            width: 80%;
            .first-h2 {
                font-family: 'Proza';
                font-weight: lighter;
                font-size: 1.4em;
                font-style: italic;
                
            }
            .first-h3 {
                margin-top: 10%;
                font-family: 'Proza';
                font-weight: lighter;
                opacity: 0.8;
                font-size: 0.9em;
            }
        }
        .first-button {
            z-index: 4;
            cursor: pointer;
            box-shadow: inset 0 0 2px 2px rgba(12, 12, 12, 0.5);
            height: 15vh;
            width: 15vh;
            border-radius: 50%;
            @include flex-column();
            color: black;
            background: rgba(195, 233, 255, 0.89);
            .first-h5 {
                // border: 1px solid black;
                // width: 80%;
                text-align: center;
                font-family: 'Sansita';
                font-size: 1.4em;
                font-weight: lighter;
            }
        }
    }

    .first-slide::before {
        content: ""; // ::before and ::after both require content
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url(../images/first-slide.jpg);
        box-shadow: inset 0 0 0 5000px rgba(0, 0, 0, 0.582);
        background-size: cover;
        background-position: top;
        opacity: 1;
        z-index: 2;
    }

    .historia {
        margin: 0;
        background: white;
        height: 100vh;
        box-shadow: 0 0 2px 0 grey;
        position: absolute;
        top: 90vh;
        
        .sound {
            width: 400vw;
            height: 5vh;
            margin-top: 4vh;
            display: flex;
            justify-content: flex-start;
            overflow: hidden;
            // border: 1px solid black;
            .group-song {
                width: 55vw;
                margin-left: 40vw;
                margin-right: 5vw;
                height: 5vh;
                position: relative;
                overflow: hidden;
                // border: 1px solid black;
                .note {
                    height: 5vw;
                    width: 5vw;
                    color: black;
                    position: absolute;
                    left: 3.5vw;
                    top: 1vw
                }
                .single {
                    @include flex-column();
                    font-family: 'Roboto';
                    align-items: flex-start;
                    width: 200vw;
                    font-size: 0.8em;
                    height: 7.5vw;
                    margin: 0 5% 0 0;
                    position: absolute;
                    left: 11vw;
                }
            }
        }
        .stories {
            width: 400vw;
            height: 65vh;
            @include flex-column();
            align-content: flex-start;
            flex-wrap: wrap;
            margin-top: 2.5vh;
            .story {
                height: 65vh;
                width: 100vw;
                @include flex-column();
                justify-content: flex-start;
                overflow: hidden;
                text-align: left;
                margin: 2.5vh 0 3vh 0;
                .img-group {
                    @include flex-column();
                    padding: 2.5vh 0;
                    gap: 2vh;
                    .img-story {
                        width: 65vw;
                        height: auto;
                    }
                    .caption-story {
                        color: #555555;
                        font-size: 0.9em;
                        font-family: 'Lato';
                        font-weight: lighter;
                        width: 60vw;
                        text-align: center;
                    }
                }
                
                .story-title {
                    font-family: 'Josefin Sans';
                    font-weight: bold;
                    font-size: 1.5em;
                    margin: 0 5% 0 5%;
                    padding: 5% 0 0 7.5%;
                    background: rgba(195, 233, 255, 0.5);
                }
                .content {
                    margin: 5% 7.5% 0 10%;
                    .paragraph {
                        line-height: 165%;
                        font-family: 'Lato';
                    }
                }
            }
        }
        .line {
            color: white;
            position: relative;
            width: 400vw;
            height: 5vh;
            background: black;
            margin-top: 7.5vh;
            .years {
                width: 400vw;
                position: absolute;
                font-family: 'Rufina';
                left: calc(50vw - 10vw);
                top: calc(2.5vh - 10vw);    
                @include flex-row();
                justify-content: flex-start;
                .circle {
                    width: 20vw;
                    height: 20vw;
                    border-radius: 50%;
                    background: black;
                    margin: 0 80vw 0 0;
                    @include flex-column();
                    font-size: 0.8em;
                }
            }
        }
    }
}

@media (min-width: 760px) {
    .wrapping {
        height: 185vh;
        
        .intro-historia {
            height: 45vh;
            justify-content: center;
            .intro-title {
                margin: 0;
                font-size: 3.2em;
                padding-top: 5%;
            }
            .intro-subtitle {
                margin: 0;
                text-align: center;
                font-size: 1.6em;
                padding: 0;
                width: 70%;
                padding: 5% 0 2.5% 0;
                
            }
        }
        .first-slide {
            top: 60vh;
            gap: 20vh;
            height: 110vh;
            .first-headers {
                // border: 1px solid white;
                margin-top: 25vh;
                width: 80%;
                .first-h2 {
                    font-size: 2.8em;
                    
                }
                .first-h3 {
                    margin-top: 2.5%;
                    font-size: 1.6em;
                }
            }
            .first-button {
                height: 12.5vh;
                width: 12.5vh;
                .first-h5 {
                    font-size: 1.8em;
                }
            }
        }
        .historia {
            top: 65vh;
            
            .sound {
                width: 400vw;
                height: 5vh;
                margin-top: 5vh;
                margin-left: 0;
                .group-song {
                    width: 35vw;
                    height: 5vh;
                    margin-left: 55vw;
                    margin-right: 10vw;
                    .note {
                        height: 2vw;
                        width: 2vw;
                        left: 16vw;
                        top: 0.4vh;
                    }
                    .single {
                        height: 2.5vw;
                        left: 19vw;
                    }
                }
            }
            .stories {
                width: 400vw;
                height: 60vh;
                margin: 4vh 0 0 0;
                .story {
                    border: none;
                    height: 60vh;
                    width: 65vw;
                    margin: 0 17.5vw;
                    position: relative;
                    .img-group {
                        width: 25vw;
                        padding: 0;
                        gap: 2vh;
                        position: absolute;
                        top: 20vh;
                        left: 0;
                        .img-story {
                            width: 25vw;
                            height: auto;
                        }
                        .caption-story {
                            width: 25vw;
                        }
                    }
                    
                    .story-title {
                        font-size: 2em;
                        margin: 0;
                        z-index: 2;
                        padding: 2.5% 0 0 5%;
                        background: none;
                    }
                    .content {
                        position: absolute;
                        width: 34vw;
                        right: -1vw;
                        top: 8vh;
                        font-size: 0.8em;
                        background: rgba(195, 233, 255, 0.5);
                        
                        padding: 5% 4% 2% 12.5% ;
                        margin: 0;

                        .paragraph {
                            line-height: 145%;
                        }
                    }
                }
            }
            .line {
                height: 5vh;
                margin: 10vh 0 0 0;
                .years {
                    left: calc(50vw - 3vw);
                    top: calc(2.5vh - 3vw);     
                    .circle {
                        width: 6vw;
                        height: 6vw;
                        margin: 0 94vw 0 0;
                    }
                }
            }
        }
    }
}